const state = {
  isAuthenticated: false,
  user: null
}

const mutations = {
  SET_USER (state, user) {
    state.user = user
    state.isAuthenticated = !!user
  }
}

const actions = {
  login ({ commit }, user) {
    commit('SET_USER', user)
  },
  logout ({ commit }) {
    commit('SET_USER', null)
  }
}

const getters = {
  isAuthenticated: state => state.isAuthenticated,
  user: state => state.user
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
